import React from 'react';
import styled from 'styled-components';

// const arr = ['CUC_IMP', 0.818142];

const Wrapper = styled.div`
  width: 100%;
  height: 5vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;
const Name = styled.p`
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: blue;
`;
const Value = styled(Name)`
  color: red;
`;

const Item = ({ arr }) => (
  <Wrapper>
    <Name>{arr[0].slice(4)}</Name>
    <Value>{arr[1]}</Value>
  </Wrapper>
);

export default Item;
